.home_logged_in_sub_banner_container {
  background: rgba(246, 246, 250, 1);
  padding: 30px 0px 0px 0px;
  width: 100%;
}

.home_logged_out_sub_banner_container {
  margin: -25px 0px 0px 0px;
  width: 100%;
}

.home_logged_in_sub_banner_wrapper {
  background: url(../../../assets/home_sub_banner/home_sub_banner.png) no-repeat center center;
  background-size: cover;
  border: 0px solid black;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5), 0 0 5px 0 rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  width: 100%;
  margin: 0px 0px 0px 0px;
  padding: 15px;
}

.home_logged_out_sub_banner_wrapper {
  background: url(../../../assets/home_sub_banner/home_sub_banner.png) no-repeat center center;
  background-size: cover;
  border: 0px solid black;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.5), 0 0 5px 0 rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  width: 100%;
  margin: 75px 0px 0px 0px;
  padding: 15px;
}

.home_sub_banner_img_container {
  display: flex;
  justify-content: center;
}

.home_sub_banner_img_container img {
  height: auto;
  width: 25px;
}

.home_sub_banner_text_container {
  color: rgba(25, 25, 25, 1);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  margin: 0px 0px 5px 0px;
  width: 100%;
}

.home_sub_banner_text_container h1 {
  font-family: 'LatoLight';
  font-size: 10pt;
  font-weight: 300;
  margin: 0px 0px 10px 0px;
  text-align: center;
}

.home_sub_banner_text_container h2 {
  font-family: 'Poppins';
  font-size: 12pt;
  font-weight: 300;
  margin: 0px 0px 0px 0px;
  text-align: center;
}

.home_sub_banner_text_container h3 {
  font-family: 'Lato';
  font-size: 18pt;
  font-weight: 600;
  margin: 0px 0px 0px 0px;
  text-align: justify;
}