.results_comment_blank {
  animation: fade_in .25s ease-in-out 1;
  align-items: center;
  background: rgba(255,255,255, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 50px;
  padding: 10px 0px 10px 0px;
  width: 100%;
  z-index: 0;
}

.results_comment {
  animation: fade_in .25s ease-in-out 1;
  align-items: center;
  background: rgba(255,255,255, 1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 50px;
  padding: 10px 0px 10px 0px;
  width: 100%;
  z-index: 0;
}

.results_all_comments {
  border-radius: 5px;
  padding: 1px;
}

.results_all_comments h3 {
  font: 400 12pt 'Lato', sans-serif;
  list-style: none;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding: 10px 0px;
  padding-inline-start: 0px;
  text-align: center;
  width: 100%;
}

.results_all_comments ul {
  border-radius: 5px;
  list-style: none;
  margin: 0px 0px 0px 0px;
  padding: 0px 0px 0px 0px;
  text-align: left;
  width: 100%;
}

.results_all_comments li {
  background: rgba(255, 255, 255, 1);
  min-width: 100%;
  max-width: 100%;
  width: 100%;
  padding: 5px;
}

.results_all_comments li p {
  font: 300 10pt 'Lato', sans-serif;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding: 5px 20px;
  height: 50px;
  z-index: -1;
}

.results_all_comments_comment_header {
  background: rgba(246, 246, 250, 1);
  border-radius: 5px 5px 0px 0px;
  align-items: flex-end;
  display: flex;
  justify-content: space-between;
  min-width: 100%;
  max-width: 100%;
  margin: 0px 0px 1px 0px;
  padding: 10px 0px 5px 0px;
  width: 100%;
}

.results_all_comments_comment_header h5 {
  font: 600 10pt 'Lato', sans-serif;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-left: 15px;
}

.results_all_comments_comment_header h5 a,
.results_all_comments_comment_header h5 a:visited {
  color: rgba(4, 108, 196, 1);
  font: 400 10pt 'Lato', sans-serif;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  text-decoration: none;
}

.results_all_comments_comment_header h5 a:hover {
  color: rgba(211, 143, 57, 1);
  font: 600 10pt 'Lato', sans-serif;
}

.results_all_comments_comment_header span {
  font: 300 7pt 'LatoItalic', sans-serif;
  margin-block-start: 0px;
  margin-block-end: 0px;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  padding-right: 15px;
}

