.selection_wrapper {
  background-image: linear-gradient(0deg, rgba(200, 200, 200, 1), rgba(175, 175, 175, 1));
  box-shadow: 0 1px 2px 0 rgba(60,64,67,.3), 0 1px 3px 1px rgba(60,64,67,.15);
  border-radius: 5px;
  margin: 20px 0px 20px 0px;
  padding: 0px;
  width: calc(700px + (1100 - 700) * ((100vw - 769px) / (1920 - 769)));
}